module.exports = {
  // 
  ser_unit (unit) {

    if(unit == 0) {
      return '斤'
    } else if (unit == 1) {
      return '扎'
    } else if (unit == 2) {
      return '磅'
    } else if (unit == 3) {
      return '两'
    } else if (unit == 4) {
      return '包'
    } else if (unit == 5) {
      return '条'
    }

    if (unit == -1) {
      return '未选择'
    }
  }
}


