
const $ = require('jquery')
const floatNum = require('./tools/float')
const storage = require('./tools/storage')

module.exports = {
  //
  inList(n, List) {
    const res = $.inArray(n, List)
    if (res < 0) { return false }
    return true
  },
  //
  indexList(n, List) {
    return $.inArray(n, List)
  },
  //
  showModal(id) {
    $('#' + id).modal('show')
  },
  //
  showToast(msg) {
    $('#dialog').find('div').html(msg)

    $('#dialog').removeClass('d-none')
    setTimeout(function() {
      $('#dialog').addClass('d-none')
    }, 3200)
  },
  //
  go(url) {
    location.href = url
  },
  //
  floatNum,
  // 
  storage
} 


